@import url(https://fonts.googleapis.com/css2?family=Asap+Condensed:wght@400;500;600&family=Orbitron:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  width: 100%;
  height: 30%;
  justify-content: space-between;
}

.header,
.title {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.location {
  font-size: 2.5rem;
}

.line {
  width: 30%;
  margin-top: 10%;
  border: solid 1px black;
}

.date {
  font-size: 1.2rem;
}

.time {
  width: 100%;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
  font-family: "Orbitron";
  font-size: 2.5rem;
}

.time span {
  display: inline-block;
  width: 20%;
}

.main {
  height: 60%;
  width: 100%;
  display: grid;
  grid-template-columns: 50% 30%;
  grid-template-rows: 30% 40% 30%;
}

.main,
.weather-temp-main,
.weather-temp {
  justify-content: center;
  align-items: center;
}

.weather-conditions,
.weather-temp-main,
.weather-temp,
.weather-update-time {
  display: flex;
}

.weather-conditions {
  grid-column: span 2;
  justify-content: left;
  align-items: center;
}
.weather-conditions img {
  width: 32%;
}
.weather-conditions span {
  text-transform: capitalize;
  font-size: 2rem;
}

.weather-temp-main {
  grid-column: span 1;
  flex-direction: column;
}
.weather-temp-main .temp-main {
  font-size: 6rem;
}
.weather-temp-main .temp-feels {
  color: rgba(0, 0, 0, 0.7);
  font-size: 1.3rem;
}

.weather-temp {
  grid-column: span 1;
  flex-direction: column;
}
.weather-temp .temp {
  color: rgba(0, 0, 0, 0.8);
  font-size: 1.7rem;
}
.weather-temp .temp-line {
  width: 32%;
  margin: 5% 0;
  border: solid 1px rgba(0, 0, 0, 0.8);
}

.weather-update-time {
  grid-column: span 2;
  justify-content: flex-end;
  align-items: flex-end;
}

.weather-update-time span {
  color: rgba(0, 0, 0, 0.5);
  font-size: 1.1rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 1.3vw;
}

body {
  background: url("https://cdn.pixabay.com/photo/2018/05/30/00/24/thunderstorm-3440450_960_720.jpg");
  background-size: cover;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  font-size: 3vh;
  font-family: "Asap Condensed", Arial, Helvetica, sans-serif;
}

#root {
  height: inherit;
}

.App {
  display: flex;
  padding: 10% 35%;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  height: inherit;
}

.wrapper {
  height: 100%;
  width: 100%;
  padding-top: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: rgba(255, 255, 255, 0.6);
}

@media screen and (max-width: 768px) {
  html {
    font-size: 4vw;
  }
  .App {
    padding: 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  html {
    font-size: 3.5vw;
  }
  .App {
    padding: 5%;
  }
}

